@import "initial-variables";

@mixin clearfix {
  &::after {
    clear: both;
    content: " ";
    display: table; } }

@mixin center($width, $height: 0) {
  position: absolute;
  @if $height != 0 {
    left: calc(50% - (#{$width} / 2));
    top: calc(50% - (#{$height} / 2)); }
  @else {
    left: calc(50% - (#{$width} / 2));
    top: calc(50% - (#{$width} / 2)); } }

@mixin fa($size, $dimensions) {
  display: inline-block;
  font-size: $size;
  height: $dimensions;
  line-height: $dimensions;
  text-align: center;
  vertical-align: top;
  width: $dimensions; }

@mixin hamburger($dimensions) {
  cursor: pointer;
  display: block;
  height: $dimensions;
  position: relative;
  width: $dimensions;
  span {
    background-color: currentColor;
    display: block;
    height: 1px;
    left: calc(50% - 8px);
    position: absolute;
    transform-origin: center;
    transition-duration: v("speed");
    transition-property: background-color, opacity, transform;
    transition-timing-function: v("easing");
    width: 16px;
    &:nth-child(1) {
      top: calc(50% - 6px); }
    &:nth-child(2) {
      top: calc(50% - 1px); }
    &:nth-child(3) {
      top: calc(50% + 4px); } }
  &:hover {
    background-color: vAlphaChange("scheme-invert", 0.05); }
  // Modifers
  &.is-active {
    span {
      &:nth-child(1) {
        transform: translateY(5px) rotate(45deg); }
      &:nth-child(2) {
        opacity: 0; }
      &:nth-child(3) {
        transform: translateY(-5px) rotate(-45deg); } } } }

@mixin overflow-touch {
  -webkit-overflow-scrolling: touch; }

@mixin placeholder {
  $placeholders: ':-moz' ':-webkit-input' '-moz' '-ms-input';
  @each $placeholder in $placeholders {
    &:#{$placeholder}-placeholder {
      @content; } } }

// Responsiveness

@mixin from($device) {
  @media screen and (min-width: $device) {
    @content; } }

@mixin until($device) {
  @media screen and (max-width: $device - 1px) {
    @content; } }

@mixin mobile {
  @media screen and (max-width: $tablet - 1px) {
    @content; } }

@mixin tablet {
  @media screen and (min-width: $tablet), print {
    @content; } }

@mixin tablet-only {
  @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
    @content; } }

@mixin touch {
  @media screen and (max-width: $desktop - 1px) {
    @content; } }

@mixin desktop {
  @media screen and (min-width: $desktop) {
    @content; } }

@mixin desktop-only {
  @if $widescreen-enabled {
    @media screen and (min-width: $desktop) and (max-width: $widescreen - 1px) {
      @content; } } }

@mixin until-widescreen {
  @if $widescreen-enabled {
    @media screen and (max-width: $widescreen - 1px) {
      @content; } } }

@mixin widescreen {
  @if $widescreen-enabled {
    @media screen and (min-width: $widescreen) {
      @content; } } }

@mixin widescreen-only {
  @if $widescreen-enabled and $fullhd-enabled {
    @media screen and (min-width: $widescreen) and (max-width: $fullhd - 1px) {
      @content; } } }

@mixin until-fullhd {
  @if $fullhd-enabled {
    @media screen and (max-width: $fullhd - 1px) {
      @content; } } }

@mixin fullhd {
  @if $fullhd-enabled {
    @media screen and (min-width: $fullhd) {
      @content; } } }

@mixin ltr {
  @if not $rtl {
    @content; } }

@mixin rtl {
  @if $rtl {
    @content; } }

@mixin ltr-property($property, $spacing, $deprecated: null) {
  @if $deprecated != null {
    $pos: "left";
    @if $deprecated {
      $pos: "right"; }
    @warn "The third argument of ltr-property was deprecated, use the full name of the property instead";
    @include ltr-property($property+"-"+$pos, $spacing); }
  @else {
    @if $rtl {
      @if str-index($property, 'left') {
        #{str-replace($property, 'left', 'right')}: $spacing; }
      @elseif str-index($property, 'right') {
        #{str-replace($property, 'right', 'left')}: $spacing; }
      @else if str-index($property, 'border-radius') {
        @if type-of($spacing) == 'list' {
          @if length($spacing) == 2 {
            #{$property}: nth($spacing, 2) nth($spacing, 1); }
          @elseif length($spacing) == 3 {
            #{$property}: nth($spacing, 2) nth($spacing, 1) nth($spacing, 2) nth($spacing, 3); }
          @elseif length($spacing) == 4 {
            #{$property}: nth($spacing, 2) nth($spacing, 1) nth($spacing, 4) nth($spacing, 3); }
          @else {
            @warn "Invalid usage of ltr-property"; } }
        @else {
          @warn "Invalid usage of ltr-property"; } }
      @elseif type-of($spacing) == 'list' and length($spacing) == 4 {
          #{$property}: nth($spacing, 1) nth($spacing, 4) nth($spacing, 3) nth($spacing, 2); }
      @else {
        @include ltr-property($property, $spacing, true); } } //Warn of deprecated use
    @else {
      @if not str-index($property, 'left') and not str-index($property, 'right') and type-of($spacing) != 'list' {
        @include ltr-property($property, $spacing, true); } //Warn of deprecated use
      @else {
        #{$property}: $spacing; } } } }

@mixin ltr-position($spacing, $right: true) {
  @warn "ltr-position was deprecated, use ltr-property with left of right as first parameter instead";
  @if $right {
    @include ltr-property("right", $spacing); }
  @else {
    @include ltr-property("left", $spacing); } }

// Placeholders

@mixin unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

%unselectable {
  @include unselectable; }

@mixin arrow($color: transparent) {
  border: 3px solid $color;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  content: " ";
  display: block;
  height: 0.625em;
  margin-top: -0.4375em;
  pointer-events: none;
  position: absolute;
  top: 50%;
  transform: rotate(-45deg);
  transform-origin: center;
  width: 0.625em; }

%arrow {
  @include arrow; }

@mixin block($spacing: $block-spacing) {
  &:not(:last-child) {
    margin-bottom: $spacing; } }

%block {
  @include block; }

@mixin delete {
  @extend %unselectable;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: vAlpha("scheme-invert", 0.2);
  border: none;
  border-radius: v("radius-rounded");
  cursor: pointer;
  pointer-events: auto;
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  font-size: 0;
  height: 20px;
  max-height: 20px;
  max-width: 20px;
  min-height: 20px;
  min-width: 20px;
  outline: none;
  position: relative;
  vertical-align: top;
  width: 20px;
  &::before,
  &::after {
    background-color: v("scheme-main");
    content: "";
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) rotate(45deg);
    transform-origin: center center; }
  &::before {
    height: 2px;
    width: 50%; }
  &::after {
    height: 50%;
    width: 2px; }
  &:hover,
  &:focus {
    background-color: vAlpha("scheme-invert", 0.3); }
  &:active {
    background-color: vAlpha("scheme-invert", 0.4); }
  // Sizes
  &.is-small {
    height: 16px;
    max-height: 16px;
    max-width: 16px;
    min-height: 16px;
    min-width: 16px;
    width: 16px; }
  &.is-medium {
    height: 24px;
    max-height: 24px;
    max-width: 24px;
    min-height: 24px;
    min-width: 24px;
    width: 24px; }
  &.is-large {
    height: 32px;
    max-height: 32px;
    max-width: 32px;
    min-height: 32px;
    min-width: 32px;
    width: 32px; } }

%delete {
  @include delete; }

@mixin loader {
  animation: spinAround 500ms infinite linear;
  border: 2px solid v("grey-lighter");
  border-radius: v("radius-rounded");
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 1em;
  position: relative;
  width: 1em; }

%loader {
  @include loader; }

@mixin overlay($offset: 0) {
  bottom: $offset;
  left: $offset;
  position: absolute;
  right: $offset;
  top: $offset; }

%overlay {
  @include overlay; }


@mixin defineVariable($var, $value) {
  #{varName($var)}: #{$value}; }

// Wrapper to output a css theme, the $all flag will output all of the variables even if they are not used, which can be useful if you want to output a theme without bulma
@mixin theme($name, $all: false) {
  @if $themeable {

    @if function-exists("_theme") {
      $name: _theme($name); }

    @if $name == "default" {

      [data-theme], :root {
        @content;

        $vars: $bulma_used_vars;
        @if $all {
          $vars: map_keys($bulma_registered_vars); }

        @each $name in $vars {
          @if isThemeable($name) {
            @include defineVariable($name, map_get($bulma_registered_vars, $name)); } } } }
    @else {
      $registered: $bulma_registered_vars;
      $selector: unquote("[data-theme=#{$name}]");

      @at-root #{$selector} {
        @content; }

      $vars: $bulma_used_vars;
      @if $all {
        $vars: map_keys($bulma_registered_vars); }

      #{$selector} {
        @each $var in $vars {
          @if map_get($bulma_registered_vars, $var) != map_get($registered, $var) {
            @include defineVariable($var, map_get($bulma_registered_vars, $var)); } } } } } }
