$media-border-color: vAlphaChange("border", 0.5) !default;
$media-spacing: 1rem;
$media-spacing-large: 1.5rem;

@include register(media-border-color, $media-border-color, true);
@include register(media-spacing, $media-spacing, true);
@include register(media-spacing-large, $media-spacing-large, true);

.media {
  align-items: flex-start;
  display: flex;
  text-align: inherit;
  .content:not(:last-child) {
    margin-bottom: 0.75rem; }
  .media {
    border-top: 1px solid v("media-border-color");
    display: flex;
    padding-top: 0.75rem;
    .content:not(:last-child),
    .control:not(:last-child) {
      margin-bottom: 0.5rem; }
    .media {
      padding-top: 0.5rem;
      & + .media {
        margin-top: 0.5rem; } } }
  & + .media {
    border-top: 1px solid v("media-border-color");
    margin-top: v("media-spacing");
    padding-top: v("media-spacing"); }
  // Sizes
  &.is-large {
    & + .media {
      margin-top: v("media-spacing-large");
      padding-top: v("media-spacing-large"); } } }

.media-left,
.media-right {
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0; }

.media-left {
  @include ltr-property("margin-right", v("media-spacing")); }

.media-right {
  @include ltr-property("margin-left", v("media-spacing")); }

.media-content {
  flex-basis: auto;
  flex-grow: 1;
  flex-shrink: 1;
  text-align: inherit; }

@include mobile {
  .media-content {
    overflow-x: auto; } }
